body {
  font-family: Roboto, sans-serif;
  color:gray;
  font-weight:300;
  overflow: hidden;
  margin:0;
}

.apartmentsearch {
  margin: 20px 0 0 30px;
  position: relative;
}

.map {
  text-align: center;
  padding-left: 0;
}

.intercom {
  position: absolute;
  bottom:30px;
  right:30px;
  height:60px;
  width:60px;
}



.searchagent {
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  bottom: 35px;
  z-index: 10;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 16px;
  font-size: 16px;
  color: rgb(76, 91, 92);
  background-color: rgb(255, 255, 255);
  right: 18px;
  height: 48px;
  border-radius: 24px;
  width: 140px;
}

.rss {
  padding-right:10px;
}
.aptlist {
  overflow-y: scroll;
  overflow-x: hidden;
  height:75vh;
  padding-right:25px;
  position: relative;
}

.containeraps {
  display: -webkit-flex;
  display: flex;
  margin-top: 7px;
}

.aptprice {
  font-size: 25px;
  color: #6699CC;
  font-weight: 900;
}

.monthutil {
  font-size: 11px;
  color: #6699CC;
  margin-left: 5px;
}

.movein {
  font-size: 14px;
  color: rgb(76, 91, 92);
  margin-top:10px;
}

@media only screen and (max-width: 990px) {
  .aptlist {
    height:70vh;
  }
}


.general-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.container {
  display: -webkit-flex;
  display: flex;
}

.container.splitleft {
  width:100%;
}

.searchicon {
  position: relative;
  top:5px;
  margin-left:30px;
  margin-right:10px;
}

.input {
  width: 60%;
}

.input .MuiInputBase-root-111 {
  width:100%;
}

.input .MuiInput-underline-102:before, .input .MuiInput-underline-102:before, .input .MuiInput-underline-102:hover {
  border: none;
  border-bottom: none;
}

.input .MuiInput-underline-102:hover:not(.MuiInput-disabled-101):not(.MuiInput-focused-100):not(.MuiInput-error-103):before {
    border-bottom: none;
}

.input .MuiInput-underline-102:before {
  border-bottom: none;
}

.phoneicon {
  margin-right:34px;
  position: relative;
  bottom:2px;
}

.phoneicon svg {
  height:12px;
}

.fontawesomephone {
  position: relative;
  top:4px;
  margin-right:5px;
}

.language {
  margin-right:10px;
}

.language img {
  position: relative;
  top:4px;
  margin-right:5px;
}

.help, .language {
  font-size:14px;
}

.bars {
  position:relative;
  bottom:5px;
}

div .bars ul {
  padding: 0;
  background-color: rgb(250, 250, 250);
}

div .bars ul li:hover {
  background-color: white;
}

.mobilelogo {
  display: none;
  position:relative;
  top:4px;
}

@media only screen and (max-width: 1023px) {
  .phoneicon {
    display:none;
  }
  .homelikelogo {
    display:none;
  }
  .mobilelogo {
    display: inline;
  }
  .searchicon {
    margin-left: 20px;
  }
}
.container-filters {
  display:-webkit-flex;
  display:flex;
  margin-top:20px;
}

.pricepaper {
  padding: 25px;
}

.modalprice {
  font-size: 11px;
  color: rgb(170, 178, 178);
}

.ameneties {
  color: rgb(170, 178, 178);
  font-size:14px;
}

.services {
  color: rgb(170, 178, 178);
  font-size:14px;
}

.guestprice {
  font-size: 11px;
  color:black;
}

.pricedisplay {
  font-size:20px;
  font-weight: 400;
  color: #6699CB;
  margin-top:3px;
}

.slider {
  margin-bottom:50px;
}

.bottommodal {
  margin-top: 50px;
}

.bottommodal, .right {
  display:-webkit-flex;
  display:flex;
  -webkit-align-items:center;
          align-items:center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.right {
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.seperatordiv {
  border-right: 1px solid rgb(238, 240, 240);
  margin-right: 5px;
  padding-right: 6px;
}

.mobilefilterwrap {
  margin-top:20px;
  border-top: 1px solid rgb(238, 240, 240);
  border-bottom: 1px solid rgb(238, 240, 240);
  margin-right: 20px;
}


.ApartmentCountTitle {
  margin-top:18px;
  font-size: 19px;
}

.containerapttitle {
  display:-webkit-flex;
  display:flex;
  -webkit-align-items: center;
          align-items: center;
}

.ApartmentCountTitle.containerapttitle button {
  margin: 0;
  padding:0;
}

.tooltip {
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}


.sortingcontainer {
  display: -webkit-flex;
  display: flex;
  margin-top:7px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.moremiles {
  color: #000066;
  font-weight: 900;
  font-size: 21px;
  margin-right:10px;
}

.collect {
  font-size:14px;
}

.sort {
  text-align: left;
  font-size: 12px;
}

.sortselection fieldset label > span {
  font-size: 13px;
  color: rgb(76, 91, 92);;
}

.sortselection {
  z-index: 999;
}

.sortfilter {
  padding-right:40px;
  margin-bottom:5px;
  position:relative;
  bottom:10px;
}

.sortingtext {
  position: relative;
  top:7px;
}

.milespermonth {
  margin-bottom:20px;
}
